<template>
  <div>
    <div class="close" @click="changeMask(false)">
      <span></span>
    </div>
    <p class="p-title">登录</p>
    <div class="phone-input">
      <input type="text" placeholder="输入手机号" maxlength="11"  v-model="phoneText">
      <span></span>
    </div>
    <div class="next" :class="{pass:isPass}" @click="pasetPhone">
      下一步
    </div>
  </div>
</template>

<script>
  import {mapMutations} from 'vuex'
  import { Toast } from 'mint-ui'
    export default {
      name: "check-telephone",
      data() {
        return {
          phoneText: '',
          isPass:false
        }
      },
      watch:{
        'phoneText':function () {
          this.testinput()
        }
      },
      methods:{
        ...mapMutations([
          'setPhone',
          'changeMask'
        ]),
        pasetPhone() {
          if(this.isPass) this.setPhone(this.phoneText)
          else{
            let instance = Toast('手机号码合适不正确')
            setTimeout(() => {
              instance.close()
            }, 1500)
          }
        },
        testinput () {
            this.phoneText=this.phoneText.replace(/[^\d]/g,'')
            let myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
            if(this.phoneText.length==11){
              let testres = myreg.test(this.phoneText)
              if(testres){
                this.isPass=true
              }
            }else {
              this.isPass=false
            }
          }
      }
    }
</script>

<style scoped>
.close{
  padding: 8px 0;
  overflow: auto;
}
.close span{
  display: inline-block;
  width: 12px;
  height: 12px;
  background-image: url(data:image/png;base64,);
  background-size: 12px 12px;
  float: right;
  margin-right: 8px;
}
.p-title{
  color: #666;
  font-size: 16px;
  text-align: center;
}
.phone-input{
  text-align: center;
}
.phone-input span{
  display: inline-block;
  border-top: 1px solid rgba(0, 0, 0, .2);
  width: 200px;
  margin: 15px 0;
}
.phone-input input{
    width: 200px;
    text-align: center;
    font-size: 18px;
    line-height: 22px;
    border: transparent;
    outline: none;
    color: rgba(0, 0, 0, .6);
}
  .next{
    height: 44px;
    line-height: 44px;
    font-size: 18px;
    background: #E7E7E7;
    color: #fff;
    text-align: center;
  }
  .pass{
    background: #4A4C5B;
  }
</style>



