<template>
  <div>
    <div class="close" @click="changeMask(false)">
      <span></span>
    </div>
    <p class="title1">输入验证码</p>
    <p class="title2">将验证码发送至{{userPhone}}</p>
    <div>
      <div class="btn" @click="countDown" v-show="isEnd">
        重新发送
      </div>
      <div class="btn" v-show="!isEnd">
        {{times}}秒后重发
      </div>
    </div>
    <div class="user-phone">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <input type="text" maxlength="4" v-model="phoneTest">
    </div>
    <p class="title2 title3">输入验证码表示同意《法律声明及隐私政策》</p>
  </div>
</template>

<script>
  import {mapGetters, mapMutations} from 'vuex'
    export default {
      name: "test-phone",
      data() {
        return{
          times:5,
          isEnd:false,
          phoneTest:''
        }
      },
      computed: {
        ...mapGetters([
          'userPhone'
        ])
      },
      methods:{
        ...mapMutations([
          'changeMask',
          'changeLogin'
        ]),
        countDown() {
          this.isEnd=false
          this.times=5
          let intval=setInterval(()=>{
            this.times--;
            if(this.times<1){
              clearInterval(intval)

              this.isEnd=true
            }
          },1000)
        }
      },
      watch:{
        phoneTest : function () {
          if(this.phoneTest.length==4){
            this.changeLogin()
            this.changeMask(false)
          }
        }
      },
      mounted:function (){
        this.countDown()
      }
    }
</script>

<style scoped>
  .close{
    padding: 8px 0;
    overflow: auto;
  }
  .close span{
    display: inline-block;
    width: 12px;
    height: 12px;
    background-image: url(data:image/png;base64,);
    background-size: 12px 12px;
    float: right;
    margin-right: 8px;
  }
  .title1{
    color: #666;
    font-size: 16px;
    text-align: center;
  }
  .title2{
    color: rgba(0, 0, 0, .4);
    text-align: center;
  }
  .title3{
    font-size: 10px;
  }
  .btn{
    box-sizing: border-box;
    width: 90px;
    height: 30px;
    margin: 20px auto;
    border: 1px solid #3e3e3e;
    border-radius: 4px;
    line-height: 30px;
    color: rgba(0,0,0,.6);
    text-align: center;
  }
  .user-phone{
    position: relative;
    text-align: center;
  }
  .user-phone span{
    display: inline-block;
    box-sizing: border-box;
    width: 50px;
    height: 50px;
    margin-right: 6px;
    border: 1px solid #e5e5e5;
    border-radius: 3px;
  }
  .user-phone input{
    position: absolute;
    left: 10px;
    top: 5px;
    border: none;
    width: 240px;
    height: 40px;
    letter-spacing: 50px;
    font-size: 20px;
    text-indent: 25px;
    background: transparent;
  }
</style>



